.section-logo-list {
    .title {
        font-weight: var(--font-body-weight);  
        padding-bottom: 30px;
        @media (min-width: 992px) {
            padding-bottom: 0;
        }
        strong { 
            font-weight: var(--font-bold);
        }
    }
    .slider-wrapper {
        margin: 0 40px;
    }
    .slider-list {
        align-items: center;
    }
    .slider-controls button {
        background-color: transparent;
        box-shadow: none;
        svg {
            color: #ffffff; 
            path {
                fill: #ffffff; 
            }
        }
        &[disabled] {
            opacity: 1;
            svg path {
                fill: #ffffff;  
            }
        }
        &.slider-prev-button {
            @media (min-width: 768px) {
                left: -20px;
            }
        }
        &.slider-next-button {
            @media (min-width: 768px) {
                right: -20px;
            }
        }
    }
}